import back from '@/components/back'
import { setInterval, clearInterval } from 'timers';

export default {
    components:{
        back
    },
    data(){
        return {
            title: '新起点互联网医院处方笺',
			user: this.$store.state.userInfo,
			ListDetails: [],
			orderInfo: this.$store.state.pInfo,
			InitInfo:null,
			info: '',
			subId:this.$store.state.chState,//判断是否是饮片或者颗粒
			Diagnoses: '',
			ValidDaysRemark: '',
			days: 1,    
			useDays:'',
			mod: '0',
			selShow:false,
			TotalDosage:this.$store.state.saveInfo && this.$store.state.saveInfo.TotalDosage?this.$store.state.saveInfo.TotalDosage:1,
			DayDosageArr:[1,2,3],
			TimesDosageArr:[1,2,3],
			addNew:false,
			newName:null,
			DayDosage:null,
			TimesDosage:null,
			company:null,
			act:null,
			act2:null,
			act3:null,
			act4:false,
			selState:null,
			dTitle:null,
			dosageArr:{},
			showConfirm:false,
			categoryItem1:[],
			UsageMethod:null,
			UsageMethodId:null,
			ContraindicationId:null,
			Contraindication:null,
			ContraindicationIdArr:[],
			ContraindicationArr:[],
			categoryItem2:[],
			cCationArr:[],
			DataSource:0,
			DataId:0,
			inputFlag:false,
			diagnosesInput:false,
			ldetailText:null,
			isNo:0,
			item: this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.PatientSex:[],
			itemList:[['男','女']],
			itemShow: false,
			itemAge: [],
			itemAgeList:[[]],
			itemAgeShow: false,
			infoPatientName:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.infoPatientName:'',
			modifyPatientInfo:this.$store.state.modifyPatientInfo,
			date: '',
			dataShow:false,
			currentdate:'',
			isYear:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.isYear:0,
			ageshow:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.age:'', 
			AgeYears:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.AgeYears:0,
			AgeMonths:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.AgeMonths:0,
			AgeDays:this.$store.state.modifyPatientInfo?this.$store.state.modifyPatientInfo.AgeDays:0,
        }
    },
    watch:{
        ListDetails(n){
            if(n.length){
                this.$refs.back.flag2 = false
            }else{
				this.$refs.back.flag2 = true
			}
        }
    },
	computed:{
	    totalPrice(){
	        let p = 0
	    	if(this.ListDetails){
	    		if(this.ListDetails.length){
	    		    this.ListDetails.forEach(v => {
						if(v.MedicationStatus != 0 && v.MedicationStatus){
							p += 0
						}else{
							if(this.subId == 2){
								p += (+v.Price) * v.PackageQuantity
							}else{
								if(v.IsRetail == 1){
									p += (+v.Price) * v.Quantity
								}else{ 
									if(Number(v.Quantity) > v.PackageCount){
										p += (Math.ceil(v.Quantity / v.PackageCount) * v.PackageCount) * v.Price
									}else{
										p += (+v.Price) * v.Quantity
									}
								}
							}
						}
	    		    })
	    		}
	    	}
	        return p.toFixed(2)
	    },
	},
    created(){
		if(this.$route.query.DataSource && this.$route.query.DataId){
			let arr = {
				DataSource:this.$route.query.DataSource,
				DataId:this.$route.query.DataId
			}
			this.$store.dispatch('pushdataSource',arr)
			this.pull(this.$route.query.DataSource,this.$route.query.DataId)
		}else if(this.$store.state.dataSource.DataSource && this.$store.state.dataSource.DataId){
			this.pull(this.$route.query.DataSource,this.$route.query.DataId)
		}else{
			this.pull(this.DataSource,this.DataId)
		}
		this.getDate()
		this.pullProcesseDemand()
		const {chList} = this.$store.state
		this.ListDetails = chList
		console.log(this.$store.state.saveInfo)
		for (var i = 14; i < 100; i++) {//1~99年龄循环
			this.itemAgeList[0].push(i+'岁')
		}
    },
    mounted(){
		this.consoleSaveInfo()
		this.diagnosesData()
		this.$refs.back.flag2 = true
    },
    beforeDestroy(){
        clearInterval(this.timer)
        this.$store.dispatch('pushDiagnoses2', this.Diagnoses)
    },
    methods:{
		infoPatientVal(){
			const inpEl = this.$refs.infoP
			inpEl.focus()
			  var length = inpEl.value.length
			  if (document.selection) {
				var range = inpEl.createTextRange()
				range.moveStart('character', length)
				range.collapse()
				range.select()
			  } else if (typeof inpEl.selectionStart === 'number' && typeof inpEl.selectionEnd === 'number') { 
				inpEl.selectionStart = inpEl.selectionEnd = length
			  }
		},
		goModifyPatientInfo(){
			let arr = {
				infoPatientName:this.infoPatientName,
				PatientSex:this.item,
				age:this.ageshow,
				isYear:this.isYear,
				AgeYears:this.AgeYears,
				AgeMonths:this.AgeMonths,
				AgeDays:this.AgeDays,
			}
			this.$store.dispatch('pushmodifyPatientInfo', arr)
		},
		pull(Source,Id){
			 this.$post('Prescription/Prescription/GetNewTCMPrescriptionInitInfo',{
				PatientId: this.orderInfo.PatientInfo.PatientId,// 就诊人Id（必填）
				DataSource: Source,//开方数据来源(0：直接开方，1：经典处方，2：历史处方，3：常用处方，4：草稿处方)
				DataId:Id,//开方数据来源Id(直接开方：0，经典处方：经典处方Id，历史处方：处方Id，常用处方：常用处方Id，草稿处方：草稿处方Id )
				DoctorId:this.user.Data.User.DoctorId,
				OrgId:this.user.Data.User.OrgId,
			}).then(o => {
				this.info = o.PatientInfo
				this.InitInfo = o.InitInfo
				
				if(!this.modifyPatientInfo){
					this.infoPatientName = o.PatientInfo.PatientName
					this.item[0] = o.PatientInfo.PatientSex == 1?'男':o.PatientInfo.PatientSex == 2?'女':'未知'

					this.checkAge(o.PatientInfo.Age)
				}else{
					this.checkAge(this.ageshow)
				}
				this.$nextTick(() => {
					this.itemShow = true
					this.itemAgeShow = true
					setTimeout(()=>{
						this.itemShow = false
						this.itemAgeShow = false
					},100)
				})
				if(Source != 0){
					if(!this.$store.state.isUsePre){
						this.subId = o.PrescriptionInfo.MedicationForm
						this.ListDetails = o.PrescriptionInfo.ListDetails
						let dosageArr = {
							TotalDosage:o.PrescriptionInfo.TotalDosage || 1,
							DayDosage:o.PrescriptionInfo.DayDosage,
							TimesDosage:o.PrescriptionInfo.TimesDosage,
							UsageMethod:o.PrescriptionInfo.UsageMethod,
							UsageMethodId:o.PrescriptionInfo.UsageMethodId,
							Contraindication:o.PrescriptionInfo.Contraindication,
							ContraindicationId:o.PrescriptionInfo.ContraindicationId,
							Diagnoses:o.PrescriptionInfo.Diagnoses,
						}
						this.TotalDosage = o.PrescriptionInfo.TotalDosage || 1
						this.$store.dispatch('pushsaveInfo', dosageArr)
						this.$store.dispatch('pushchList', o.PrescriptionInfo.ListDetails)
						this.$store.dispatch('pushchState', this.subId)
						this.consoleSaveInfo()
					}
					this.$store.dispatch('pushisUsePre',true)
				}
				this.ldetail()
			})
		},
		checkAge(val){
			if(!val){ //如果没有年龄不做判断
				return;
			}
			let age = val.split('岁')
			if(val.indexOf("岁") === -1 || Number(age[0]) < 1){ //小于1岁
				if(val.indexOf("个月") === -1){
					val = '0个月' + val
				}
				this.itemAge[0] = val
				this.ageshow = val
				this.AgeYears = 0
				let Months = val.split("个月")
				this.AgeMonths = Months[0] == 0 ? '' : Months[0]
				let Days = Months[1].split("天")
				this.AgeDays = Days[0]
			}else{
				this.itemAge[0] = age[0]+'岁'
				if(Number(age[0]) < 14){ //小于14岁
					this.isYear = 1; //按年月日
					this.AgeYears = age[0]
					if(val.indexOf("个月") != -1){
						let Months = age[1].split("个月")
						this.AgeMonths = Months[0]
						this.ageshow = age[0]+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeMonths = ''
						this.ageshow = age[0]+'岁'
					}
				}else{
					if(val.indexOf("个月") != -1){
						this.isYear = 1; //按年月日
						this.AgeYears = age[0]
						let Months = age[1].split("个月")
						this.AgeMonths = Months[0]
						this.ageshow = age[0]+'岁'+this.AgeMonths+'个月'
					}else{
						this.isYear = 0; //按年龄
						this.AgeYears = age[0]
						this.AgeMonths = ''
						this.ageshow = val
					}
				}
			}
			this.calculationData(val)
		},
		calculationData(val){
			let date = new Date();
			let year = Number(date.getFullYear());
			let month = Number(date.getMonth() + 1);
			let strDate = Number(date.getDate());
			let age = val.split('岁')
			if(val.indexOf("岁") === -1 || Number(age[0]) < 1){
				age[0] = 0
			}
			if(strDate - this.AgeDays < 0){   //天小于0
				if(month-this.AgeMonths < 1){  // 月小于0
					this.date = (year-1-age[0])+'-'+(month+12-1-this.AgeMonths)+'-'+(strDate+30-this.AgeDays)
				}else{
					this.date = (year-age[0])+'-'+(month-this.AgeMonths-1)+'-'+(strDate+30-this.AgeDays)
				}
			}else{  //天大于等于0
				if(month-this.AgeMonths < 1){  // 月小于0
					this.date = (year-1-age[0])+'-'+(month+12-this.AgeMonths)+'-'+(strDate-this.AgeDays)
				}else{
					this.date = (year-age[0])+'-'+(month-this.AgeMonths-1)+'-'+(strDate-this.AgeDays)
				}
			}
				//2个月2天  2018-11-14    2019-1-16
			console.log(this.date)
			console.log(this.AgeYears)
			console.log(this.AgeMonths)
			console.log(this.AgeDays)
		},
		onChange(val){
			let val_ = val.join('')
			let valAge = val.join('')
			if(val_.indexOf('岁') === -1 && (val_.indexOf('天') != -1 || val_.indexOf('月') != -1)){
				val_ = 1 + '岁'
				valAge = 1 + '岁'
			}
			this.ageshow = val_
			this.AgeYears = valAge.split('岁')[0]
			this.AgeMonths = 0
			this.AgeDays = 0
			console.log(this.AgeYears)
			console.log(this.AgeMonths)
			console.log(this.AgeDays)
		},
		changeData(val){
			let date = new Date();
			let year = Number(date.getFullYear());
			let month = Number(date.getMonth() + 1);
			let strDate = Number(date.getDate());
			let val_ = val.split('-')
			console.log(val)
			//1岁1个月29天  2019-2-29    2019-12-13
			if(strDate - Number(val_[2]) < 0){  //天
				if(month - Number(val_[1]) < 1){  //月
					this.AgeMonths = month + 12 - 1 - Number(val_[1])
					this.AgeDays = strDate + 30 - Number(val_[2]) 
					if(year - Number(val_[0]) === 0){
						this.AgeYears = 0
						this.AgeMonths = 0
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) < 2){ //年
						this.AgeYears = 0
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) <= 14){
						this.AgeYears = year - Number(val_[0]) -1
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0]) -1
						this.ageshow = this.AgeYears+'岁'
					}
				}else{
					this.AgeMonths = month - 1 - Number(val_[1])
					this.AgeDays = strDate + 30 - Number(val_[2]) 
					if(year - Number(val_[0]) === 0){
						this.AgeYears = 0
						//this.AgeMonths = 0
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) <= 1){ //年
						this.AgeYears = 0
						if(this.AgeMonths == 0){
							this.AgeYears = year - Number(val_[0]) -1
							this.AgeMonths = 12
							this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
						}else{
							this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
						}
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'
					}
				}
			}else if(strDate - Number(val_[2]) === 0){
				if(month - Number(val_[1]) === 0){
					this.AgeMonths = month - Number(val_[1])
					this.AgeDays = strDate - Number(val_[2]) 
					if(year - Number(val_[0]) <= 1){ //年
						this.AgeYears = 0
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'
					}
				}else if(month - Number(val_[1]) < 1){  //月
					this.AgeMonths = month + 12  - Number(val_[1])
					this.AgeDays = strDate - Number(val_[2]) 
					if(year - Number(val_[0]) <= 1){ //年
						this.AgeYears = 0
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0]) -1
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0]) -1
						this.ageshow = this.AgeYears+'岁'
					}
				}else{//1岁1个月29天  2018-11-13    2019-12-13
					this.AgeMonths = month - Number(val_[1])
					this.AgeDays = strDate - Number(val_[2]) 
					if(year - Number(val_[0]) <= 1){ //年
						this.AgeYears = 0
						if(this.AgeMonths == 0){
							this.AgeYears = year - Number(val_[0])
							this.AgeMonths = 12
							this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
						}else{
							this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
						}
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'
					}
				}

			}else{   //1岁1个月29天  2019-12-12    2019-12-13
				if(month - Number(val_[1]) === 0){
					this.AgeMonths = month - Number(val_[1])
					this.AgeDays = strDate - Number(val_[2]) 
					if(year - Number(val_[0]) < 1){ //年
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'
					}
				}else if(month - Number(val_[1]) < 1){  //月
					this.AgeMonths = month + 12 - Number(val_[1])
					this.AgeDays = strDate - Number(val_[2]) 
					if(year - Number(val_[0]) <= 1){ //年
						if(this.AgeMonths == 12){
							this.AgeYears = year - Number(val_[0])
							this.AgeMonths = 0
							this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
						}else{
							this.AgeYears = 0
							this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
						}
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0]) -1
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0]) -1
						this.ageshow = this.AgeYears+'岁'
					}
				}else{ //1岁1个月29天  20017-11-12    2019-12-13
					this.AgeMonths = month - Number(val_[1])
					this.AgeDays = strDate - Number(val_[2]) 
					if(year - Number(val_[0]) < 1){ //年
						this.AgeYears = 0
						this.ageshow = this.AgeMonths+'个月'+this.AgeDays+'天'
					}else if(year - Number(val_[0]) < 14){
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'+this.AgeMonths+'个月'
					}else{
						this.AgeYears = year - Number(val_[0])
						this.ageshow = this.AgeYears+'岁'
					}
				}
			}
		},
		changeIsYear(){
			if(this.isYear){
				this.itemAgeShow = false
				this.dataShow = true
				setTimeout(()=>{
					document.getElementById("transfHtml").innerText = '按岁'
					document.getElementById("transf").style.display="flex";
					document.getElementById("transf").style.bottom="267px";
					document.getElementById("transf").style.width="90px";
					document.getElementById("transf").style.marginLeft="-45px";
				},300)
				
			}else{
				this.itemAgeShow = true
				this.dataShow = false
				setTimeout(()=>{
					document.getElementById("transfHtml").innerText = '14周岁以下请按年月日选择'
					document.getElementById("transf").style.display="flex";
					document.getElementById("transf").style.bottom="247px";
					document.getElementById("transf").style.width="210px";
					document.getElementById("transf").style.marginLeft="-105px";
				},300)
			}
		},
		transfHide(){
			document.getElementById("transf").style.display="none";
		},
		transfIsYear(){
			if(this.isYear){
				this.isYear = 0 
			}else{
				this.isYear = 1
			}
			this.changeIsYear()
		},
		ldetail(){
			let ldetailArr = []
			this.ListDetails.forEach(v => {
				if(v.MedicationStatus != 0 && v.MedicationStatus){
					ldetailArr.push(v.Name)
				}
			})
			if(ldetailArr && ldetailArr.length){
				this.isNo = 1
			}else{
				this.isNo = 0
			}
			console.log(this.ListDetails)
			this.ldetailText = ldetailArr.join(',')
		},
		diagnosesInputMethods(n,val){
			if(n == 2){
				this.$post('Prescription/Prescription/DelDiagnosesHistory',{
				    Id: val.Id
				}).then(o => {
				    this.diagnosesData()
				})
			}else{
				if(this.diagnosesArr.length != 0){
					this.diagnosesInput = true
				}
			}	
		},
		diagnosesData(){
			this.$get('Prescription/Prescription/GetDiagnosesHistoryList',{
			    DoctorId:this.user.Data.User.DoctorId,
				PrescriptionType:2
			}).then(o => {
			    this.diagnosesArr = o
			})
		},
		diagnosesVal(val){
			this.Diagnoses = val.Diagnoses
			this.$bridge.callhandler('printdata',val.Diagnoses+'v1') 
			if(this.$store.state.saveInfo){
				this.$store.state.saveInfo.Diagnoses = val.Diagnoses
			}else{
				this.dosageArr.Diagnoses = val.Diagnoses
				this.$store.dispatch('pushsaveInfo', this.dosageArr)
				this.$bridge.callhandler('printdata',val.Diagnoses+'v2') 
			}
			this.$bridge.callhandler('printdata',val.Diagnoses+'v3') 
		},
		changeCount(n){
			event.preventDefault();
			let that = this;
			if(n == 2){
				if(this.Diagnoses != ""){
					setTimeout(function(){
						that.diagnosesInput = false
					},100)
				}
			}else{
				setTimeout(function(){
					that.diagnosesInput = false
				},100)
			}
		},
		pullProcesseDemand(){
			this.$post('PlatFormAPI/Category/QueryCategoryItem',{
			    CategoryIds:[55,56]
			}).then(o => {
				this.categoryItem1 = o[0].Items
				o[1].Items.forEach((v,k) => {
					 o[1].Items[k].state = false
				})
				this.categoryItem2 = o[1].Items
			})
		},
		edit(){
			this.goModifyPatientInfo()
			this.$router.push(`/chooseDrugs`) 
		},
		add(){
			this.TotalDosage = Math.floor(this.TotalDosage) + 5
		},
		reduce(){
			if(this.TotalDosage > 1 ){
				this.TotalDosage = Math.floor(this.TotalDosage) -1
			}
		},
		daysDosage(i,idx){
			this.act = idx
			this.DayDosage = i
		},
		daysCi(o,idx){
			this.act2 = idx
			this.TimesDosage = o
		},
		Usage(i,idx){
			this.act3 = idx
			this.UsageMethod = i.CategoryItemName
			this.UsageMethodId = i.CategoryItemValue
		},
		Usage2(i,idx){
			this.ContraindicationArr = []
			this.ContraindicationIdArr = []
			const browser = navigator.userAgent
			if(browser.indexOf('iPhone') !== -1){
				if(i.state){
					if(i.CategoryItemName == '无'){
						this.cCationArr = []
						this.categoryItem2.forEach((v,k) => {
							this.categoryItem2[k].state = false
						})
					}else{
						this.cCationArr.forEach((v,k) => {
							 if(v.CategoryItemName == '无'){
								this.cCationArr.splice(k, 1)
							 }
						})
						this.categoryItem2.forEach((v,k) => {
							if(v.CategoryItemName == '无'){
								this.categoryItem2[k].state = false
							}
						})
					}
					i.state = true
					this.cCationArr.push(i)
				}else{
					i.state = false
					this.cCationArr.forEach((v,k) => {
						if(i.CategoryItemValue == v.CategoryItemValue){
							this.cCationArr.splice(k, 1)
						}
					})
				}
			}else{
				if(!i.state){
					if(i.CategoryItemName == '无'){
						this.cCationArr = []
						this.categoryItem2.forEach((v,k) => {
							this.categoryItem2[k].state = false
						})
					}else{
						this.cCationArr.forEach((v,k) => {
							 if(v.CategoryItemName == '无'){
								this.cCationArr.splice(k, 1)
							 }
						})
						this.categoryItem2.forEach((v,k) => {
							if(v.CategoryItemName == '无'){
								this.categoryItem2[k].state = false
							}
						})
					}
					i.state = true
					this.cCationArr.push(i)
				}else{
					i.state = false
					this.cCationArr.forEach((v,k) => {
						if(i.CategoryItemValue == v.CategoryItemValue){
							this.cCationArr.splice(k, 1)
						}
					})
				}
			}
			if(this.cCationArr && this.cCationArr.length){
				this.cCationArr.forEach((v,k) => {
					this.ContraindicationArr[k] = v.CategoryItemName
					this.ContraindicationIdArr[k] = v.CategoryItemValue
				})
			}
			this.Contraindication = this.ContraindicationArr.join(',')
			this.ContraindicationId = this.ContraindicationIdArr.join(',')
		},
		medicineC(i){
			this.selShow = true
			this.selState = i
			if(this.selState == 1){
				this.dTitle = '剂数'
			}else if(this.selState == 2){
				this.dTitle = '请选择用药方法'
			}else if(this.selState == 3){
				this.dTitle = '请选择服药期间禁忌'
			}
		},
		selMcancel(){
			this.selShow = false
			this.addNew = false
		},
		selConfirm(){
			this.dosageArr.Diagnoses = this.Diagnoses
			if(this.selState == 1){
				if(!this.TotalDosage){ 
					return this.$toast('请输入总剂数')
				}
				if(!this.DayDosage){
					return this.$toast('请选择每日剂数')
				}
				if(!this.TimesDosage){
					return this.$toast('请选择每剂次数')
				}
				if(this.$store.state.saveInfo  ){ //&& this.$store.state.saveInfo.DayDosage
					this.$store.state.saveInfo.TotalDosage = this.TotalDosage
					this.$store.state.saveInfo.DayDosage = this.DayDosage
					this.$store.state.saveInfo.TimesDosage = this.TimesDosage
				}else{
					this.dosageArr.TotalDosage = this.TotalDosage
					this.dosageArr.DayDosage = this.DayDosage
					this.dosageArr.TimesDosage = this.TimesDosage
					this.$store.dispatch('pushsaveInfo', this.dosageArr)
				}
				this.$refs.dosage1.innerHTML = '总共'+this.TotalDosage+'剂，每日'+this.DayDosage+'剂，每剂'+this.TimesDosage+'次'
			}else if(this.selState == 2){
				if(this.$store.state.saveInfo ){ // && this.$store.state.saveInfo.UsageMethod
					this.$store.state.saveInfo.UsageMethod = this.UsageMethod
					this.$store.state.saveInfo.UsageMethodId = this.UsageMethodId
				}else{
					this.dosageArr.UsageMethod = this.UsageMethod
					this.dosageArr.UsageMethodId = this.UsageMethodId
					this.$store.dispatch('pushsaveInfo', this.dosageArr)
				}
				this.$refs.dosage2.innerHTML = this.UsageMethod
				if(!this.UsageMethod){ 
					this.$refs.dosage2.innerHTML = '点击选择用药方法'
				}
			}else if(this.selState == 3){ //&& this.$store.state.saveInfo.Contraindication
				if(this.$store.state.saveInfo  ){
					this.$store.state.saveInfo.Contraindication = this.Contraindication
					this.$store.state.saveInfo.ContraindicationId = this.ContraindicationId
				}else{
					this.dosageArr.Contraindication = this.Contraindication
					this.dosageArr.ContraindicationId = this.ContraindicationId
					this.$store.dispatch('pushsaveInfo', this.dosageArr)
				}
				this.$refs.dosage3.innerHTML = this.Contraindication
				if(!this.Contraindication){ 
					this.$refs.dosage3.innerHTML = '点击选择（可多选）'
				}
			}
			this.selShow = false
			this.addNew = false
		},
		DiaMethod(){
			if(this.inputFlag){
			    return
			}
			if(this.Diagnoses == ""){
				this.diagnosesInput = true
			}else{
				this.diagnosesInput = false
			}
			if(this.$store.state.saveInfo  && this.$store.state.saveInfo.Diagnoses){
				this.$store.state.saveInfo.Diagnoses = this.Diagnoses
			}else{
				this.dosageArr.Diagnoses = this.Diagnoses
				this.$store.dispatch('pushsaveInfo', this.dosageArr)
			}
		},
		consoleSaveInfo(){
			if(this.$store.state.saveInfo && this.$store.state.saveInfo.DayDosage){
				this.$refs.dosage1.innerHTML = '总共'+this.$store.state.saveInfo.TotalDosage+'剂，每日'+this.$store.state.saveInfo.DayDosage+'剂，每剂'+this.$store.state.saveInfo.TimesDosage+'次'
				this.DayDosage = this.$store.state.saveInfo.DayDosage
			}else{
				this.$refs.dosage1.innerHTML = '点击选择剂数'
			}
			if(this.$store.state.saveInfo && this.$store.state.saveInfo.UsageMethod){
				this.$refs.dosage2.innerHTML = this.$store.state.saveInfo.UsageMethod
			}else{
				this.$refs.dosage2.innerHTML = '点击选择用药方法'
			}
			if(this.$store.state.saveInfo && this.$store.state.saveInfo.Contraindication){
				this.$refs.dosage3.innerHTML = this.$store.state.saveInfo.Contraindication
			}else{
				this.$refs.dosage3.innerHTML = '点击选择（可多选）'
			}
			if(this.$store.state.saveInfo && this.$store.state.saveInfo.Diagnoses){
				this.Diagnoses = this.$store.state.saveInfo.Diagnoses
			}
		},
		jiDosage(v){
			this.addNew = true
			this.company = v
		},
		MConfirm(){
			if(!this.newName){
			    return this.$toast('请输入自定义内容')
			}
			if(this.company == 'ji'){
				if(isNaN(this.newName) || Number(this.newName) < 1){
				    return this.$toast('请输入数字且数字大于0')
				}
				this.DayDosageArr.push(this.newName)
			}else if(this.company == 'ci'){
				if(isNaN(this.newName) || Number(this.newName) < 1){
				    return this.$toast('请输入数字且数字大于0')
				}
				this.TimesDosageArr.push(this.newName)
			}else if(this.company == 'yf'){
				if(!this.newName){
				    return this.$toast('请输入用药方法')
				}
				let a = {
					CategoryItemName:this.newName,
					CategoryItemValue:0
				}
				this.categoryItem1.push(a)
			}
			this.newName = ''
			this.addNew = false
		},
		Mcancel(){
			this.addNew = false
		},
        getDate(){
            var date = new Date();
            var seperator1 = "-";
            var year = date.getFullYear();
            var month = date.getMonth() + 1;
            var strDate = date.getDate();
            if (month >= 1 && month <= 9) {
                month = "0" + month;
            }
            if (strDate >= 0 && strDate <= 9) {
                strDate = "0" + strDate;
            }
            this.currentdate = year + seperator1 + month + seperator1 + strDate;
        },
        pop(idx){
            this.showConfirm = true
            this.popIdx = idx
        },
        confirm(){
            this.ListDetails.splice(this.popIdx, 1)
        },
        modify(idx){
            this.$store.dispatch('pushcMed', this.ListDetails[idx])
            this.$router.push(`/modifyCon?idx=${idx}&&back=-1`)
		},
		temporary(){
            if(!(this.ListDetails && this.ListDetails.length)){
                return this.$toast('请添加药品')
            }
			if(this.isNo == 1){
				 return this.$toast(this.ldetailText+' 药品库存不足，请编辑药品')
			}
			const List = []
			this.ListDetails.forEach(v => {
			    List.push({
					MedicationId:v.MedicationId,//药品Id
					Name:v.Name,// 药品名
					Quantity:v.Quantity || 1,//包装数量
					ProcesseDemand:v.ProcesseDemand || '',//煎煮特殊要求
					ProcesseDemandId:v.ProcesseDemandId || 0,  //煎煮特殊要求Id
					PackageQuantity:v.PackageQuantity || 1,
					PrepareDemandId:v.PrepareDemandId || 0, // 炮制要求Id
					PrepareDemand:v.PrepareDemand || '',//炮制要求
					Remark:'',
			    })
			})
			let saveIn = this.$store.state.saveInfo
            this.$post('Prescription/Prescription/SaveTCMTempPrescription',this.$pick({
				MedicationCompanyId:this.InitInfo.MedicationCompanyId,//医药公司Id(必填)
				PatientId:this.orderInfo.PatientInfo.PatientId,//16就诊人Id(必填)
				PatientName:this.infoPatientName,//HUU	就诊人名称(必填)
				ApplicantId:this.orderInfo.ApplicantId,//申请人Id(会员AccountId)
				ApplicantName:this.orderInfo.ApplicantName,//	申请人名称
				DoctorId: this.user.Data.User.DoctorId,
				DoctorName:this.user.Data.User.DoctorName,//huatuo
				Diagnoses: (saveIn && saveIn.Diagnoses)?saveIn.Diagnoses:'',//this.Diagnoses,//	临床诊断
				MedicationForm:this.subId,//	药态（1：饮片，2：颗粒）
				TotalDosage:(saveIn && saveIn.TotalDosage)?saveIn.TotalDosage:0,//	总剂数
				DayDosage:(saveIn && saveIn.DayDosage)?saveIn.DayDosage:0,//	每日剂数
				TimesDosage:(saveIn && saveIn.TimesDosage)?saveIn.TimesDosage:0,//	每剂次数
				UsageMethodId:(saveIn && saveIn.UsageMethodId)?saveIn.UsageMethodId:0,//	用药方法Id
				UsageMethod:(saveIn && saveIn.UsageMethod)?saveIn.UsageMethod:'',//	用药方法
				ContraindicationId:(saveIn && saveIn.ContraindicationId)?saveIn.ContraindicationId:0,//	用药禁忌Id
				Contraindication:(saveIn && saveIn.Contraindication)?saveIn.Contraindication:'',//	用药禁忌
				AdditionalRemark:this.ValidDaysRemark,//	补充说明
				Remark:'',
				ListDetails:List,// 处方药清单
            })).then(o => {
				this.$toast('暂存成功')
                const data = '123'
                this.$bridge.callhandler('BackToIM', data)
            })
		},
        send(){
			if(!this.infoPatientName){
				return this.$toast('请填写姓名')
			}
			if(this.item[0] != '男' && this.item[0] != '女'){
				return this.$toast('请选择性别')
			}
			if(!this.ageshow){
				return this.$toast('请选择年龄')
			}
            if(!(this.ListDetails && this.ListDetails.length)){
                return this.$toast('请添加药品')
            }
            if(!this.Diagnoses){
                return this.$toast('请填写临床诊断')
            }
			if(!this.DayDosage){
                return this.$toast('请先添加剂数')
            }
			if(this.isNo == 1){
				 return this.$toast(this.ldetailText+' 药品库存不足，请编辑药品')
			}
            const List = []
			this.ListDetails.forEach(v => {
			    List.push({
					MedicationId:v.MedicationId,//药品Id
					Quantity:v.Quantity || 1,//包装数量
					ProcesseDemand:v.ProcesseDemand,//煎煮特殊要求
					ProcesseDemandId:v.ProcesseDemandId || 0,
					PackageQuantity:v.PackageQuantity || 1,
			    })
			})
			console.log(this.$store.state.saveInfo)
            // const ApplyId = this.orderInfo && this.orderInfo.Method ? this.orderInfo.Id : null
            this.$post('Prescription/Prescription/SaveTCMPrescription',this.$pick({
				Id:'',//自增Id
				ApplyId:0,	//续方申请Id
				No:this.InitInfo.PrescriptionNo,
				MedicationCompanyId:this.InitInfo.MedicationCompanyId,//医药公司Id(必填)
				PatientId:this.orderInfo.PatientInfo.PatientId,//16就诊人Id(必填)
				PatientName:this.infoPatientName,//HUU	就诊人名称(必填)
				PatientSex:this.item[0]=='男'?1:this.item[0]=='女'?2:0,
				//Age:this.itemAge[0],
				AgeYears:this.AgeYears,
				AgeMonths:this.AgeMonths,
				AgeDays:this.AgeDays,
				ApplicantId:this.orderInfo.ApplicantId,//申请人Id(会员AccountId)
				ApplicantName:this.orderInfo.ApplicantName,//	申请人名称
				DoctorId: this.user.Data.User.DoctorId,
				DoctorName:this.user.Data.User.DoctorName,//huatuo
				OrgId: this.user.Data.User.OrgId,
				Diagnoses: this.$store.state.saveInfo.Diagnoses,//this.Diagnoses,//	临床诊断
				ValidDays:1,//	有效天数(必填)
				ValidDaysRemark:'',//	有效期备注
				MedicationForm:this.subId,//	药态（1：饮片，2：颗粒）
				TotalDosage:this.$store.state.saveInfo.TotalDosage,//	总剂数
				DayDosage:this.$store.state.saveInfo.DayDosage,//	每日剂数
				TimesDosage:this.$store.state.saveInfo.TimesDosage,//	每剂次数
				UsageMethodId:this.$store.state.saveInfo.UsageMethodId,//	用药方法Id
				UsageMethod:this.$store.state.saveInfo.UsageMethod,//	用药方法
				ContraindicationId:this.$store.state.saveInfo.ContraindicationId,//	用药禁忌Id
				Contraindication:this.$store.state.saveInfo.Contraindication,//	用药禁忌
				AdditionalRemark:this.ValidDaysRemark,//	补充说明
				IsSaveForOftenUsed:this.mod,//	是否存为常用处方
				ListDetails:List,// 处方药清单
            })).then(o => {
				if(o.IsNeedSign){
					let parama = {
						UniqueId:o.UniqueId,
						PrescriptionId:o.PrescriptionId,
						MedicationType:2
					}
				    this.$bridge.callhandler('signId',JSON.stringify(parama) , (data) => {
				        this.$router.replace(`/success?name=${this.orderInfo.PatientInfo.PatientName}&NO=${this.InitInfo.PrescriptionNo}`)
				    })
				}
				else{
				    this.$router.replace(`/success?name=${this.orderInfo.PatientInfo.PatientName}&NO=${this.InitInfo.PrescriptionNo}`)
				}
            })
        }
    }
}



