		checkAge: function checkAge(val) {
			if (!val) {
				//如果没有年龄不做判断
				return;
			}
			var age = val.split('岁');
			if (val.indexOf("岁") === -1 || Number(age[0]) < 1) {
				//小于1岁
				if (val.indexOf("个月") === -1) {
					val = '0个月' + val;
				}
				this.itemAge[0] = val;
				this.ageshow = val;
				this.AgeYears = 0;
				var Months = val.split("个月");
				this.AgeMonths = Months[0] == 0 ? '' : Months[0];
				var Days = Months[1].split("天");
				this.AgeDays = Days[0];
			} else {
				this.itemAge[0] = age[0] + '岁';
				if (Number(age[0]) < 14) {
					//小于14岁
					this.isYear = 1; //按年月日
					this.AgeYears = age[0];
					if (val.indexOf("个月") != -1) {
						var _Months = age[1].split("个月");
						this.AgeMonths = _Months[0];
						this.ageshow = age[0] + '岁' + this.AgeMonths + '个月';
					} else {
						this.AgeMonths = '';
						this.ageshow = age[0] + '岁';
					}
				} else {
					if (val.indexOf("个月") != -1) {
						this.isYear = 1; //按年月日
						this.AgeYears = age[0];
						var _Months2 = age[1].split("个月");
						this.AgeMonths = _Months2[0];
						this.ageshow = age[0] + '岁' + this.AgeMonths + '个月';
					} else {
						this.isYear = 0; //按年龄
						this.AgeYears = age[0];
						this.AgeMonths = '';
						this.ageshow = val;
					}
				}
			}
			this.calculationData(val);
		},
		calculationData: function calculationData(val) {
			var date = new Date();
			var year = Number(date.getFullYear());
			var month = Number(date.getMonth() + 1);
			var strDate = Number(date.getDate());
			var age = val.split('岁');
			if (val.indexOf("岁") === -1 || Number(age[0]) < 1) {
				age[0] = 0;
			}
			if (strDate - this.AgeDays < 0) {
				//天小于0
				if (month - this.AgeMonths < 1) {
					// 月小于0
					this.date = year - 1 - age[0] + '-' + (month + 12 - 1 - this.AgeMonths) + '-' + (strDate + 30 - this.AgeDays);
				} else {
					this.date = year - age[0] + '-' + (month - this.AgeMonths - 1) + '-' + (strDate + 30 - this.AgeDays);
				}
			} else {
				//天大于等于0
				if (month - this.AgeMonths < 1) {
					// 月小于0
					this.date = year - 1 - age[0] + '-' + (month + 12 - this.AgeMonths) + '-' + (strDate - this.AgeDays);
				} else {
					this.date = year - age[0] + '-' + (month - this.AgeMonths - 1) + '-' + (strDate - this.AgeDays);
				}
			}
			//2个月2天  2018-11-14    2019-1-16
			console.log(this.date);
			console.log(this.AgeYears);
			console.log(this.AgeMonths);
			console.log(this.AgeDays);
		},
